import index from '../../common/index.js';
import md5 from '../../common/SDK/md5.min.js';
import {
	mapMutations
} from 'vuex';

export default {
	data() {
		return {
			name: '',
			pass: '',
			logining: false
		}
	},
	onLoad() {

	},
	methods: {
		...mapMutations(['login']),
		inputChange(e) {
			const key = e.currentTarget.dataset.key;
			this[key] = e.detail.value;
		},
		navBack() {
			uni.navigateBack();
		},
		toRegist() {

		},
		async toLogin() {
			this.logining = false;
			const {
				name,
				pass
			} = this;
			uni.hideKeyboard();
			//验证手机号码
			if (name == "") {
				uni.showToast({
					title: '请输入用户名',
					icon: "none"
				});
				return false;
			}
			//验证手机号码
			if (pass == "") {
				uni.showToast({
					title: '请输入密码',
					icon: "none"
				});
				return false;
			}


			uni.request({
				url: index.api + '/index.php/Home/Login/index',
				method: 'post',
				data: {
					user: name,
					pass: pass
				},
				header: {
					'Content-type': 'application/x-www-form-urlencoded'
				},
				success: res => {
					console.log(res);
					const content = res.data.data;
					//console.log(md5(content.uid + content.hdid + "ximi"))   //token
					//储存用户信息
					uni.setStorage({
						key: 'userinfo',
						data: {
							uid: content.uid,
							hdid: content.hdid,
							username: content.uname,
							token: md5(content.uid + content.hdid + "ximi"),
							isvip:content.isvip
						}
					})
					
					uni.switchTab({
						url: '/pages/index/index'
					});

				},
				fail: res => {
					console.log(res)
				},
				complete: () => {}
			});
		}
	},

}
